.wrap {
	width: 100%;
	padding: 80px;
	padding-top: 15px;
	display: flex;
	justify-content: space-between;
}

#vids {
	width: 100%;
	height: 100%;
	// background: lightblue;

	.swiper {
		height: 100%;
		padding: 2vw 0px;

		.swiper-slide {
			width: 100%;

			.inner {
				width: 100%;
				height: 100%;

				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				align-content: center;
				color: #333;
				font: bold 50px/1 'arial';
				transform: scale(0.8);
				opacity: 0.5;
				transition: opacity 0.5s, transform 0.5s;
				padding: 10px;

				.pic {
					width: 100%;
					height: 40%;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
					.fa-youtube {
						font-size: 50px;
						color: white;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						transition: 0.5s;
						cursor: pointer;
					}
				}
				h2 {
					font: normal 40px/1 'work sans';
					color: #111;
					margin-top: 15px;
				}
			}
		}
		//활성화 패널 스타일
		.swiper-slide-active {
			.inner {
				transform: scale(1);
				opacity: 1;
			}
		}

		.swiper-pagination {
			width: 100%;
			bottom: 30px;

			.swiper-pagination-bullet {
				width: 10px;
				height: 10px;
				border-radius: 5px;
				background: #fff;
				margin: 0px 10px;
				opacity: 0.5;
				transition: 0.5s;
			}
			.swiper-pagination-bullet-active {
				width: 100px;
				opacity: 1;
			}
		}

		// .swiper-button-next {
		// 	top: auto;
		// 	bottom: 20px;
		// 	left: auto;
		// 	right: 50px;
		// 	transform: scale(0.6);
